.productdisplay {
    font-family: "Noto Sans", sans-serif;;
}

.productdisplay-left img{
    width: 586px;
    height: 700px; 
}

.productdisplay-img {
    display: flex;
    flex-direction: column;
}

.productdisplay {
    margin: 40px 30px;
    display: flex;
    gap: 80px;
} 

.productdisplay-right {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-top: 20px;   
}

.productdisplay-right-star{
    font-size: 20px;
    margin-top: -30px;
}

.review-rate {
    color: #616161;

}

.productdisplay-right-size {
    display: flex;
    flex-direction: column;
}


.sizes {
    display: flex;
    gap: 20px;
    font-size: 20px;
}

.sizebackground {
    border-radius: 10px;
    padding: 20px 30px;
    border: pink solid 3px;
    cursor: pointer;
    transition: background 0.1s;
}

.sizebackground:hover {
    background: pink;
}


.pink {
    background: pink
}

.productdisplay-right-star {
    display: flex;
    align-items: center;
    gap: 10px;
}

.productdisplay-right-prices {
    display: flex;
    gap: 20px;
    font-size: 20px;
}

.productdisplay-right-prices .productdisplay-right-price-old {
    text-decoration: line-through;
    color: #8c8c8c;
}

.productdisplay button {
    margin-top: 20px;
    border: none;
    outline: none;
    background: pink;
    padding: 30px;
    font-size: 20px;
    border-radius: 10px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    font-weight: 600;
    color: white;
    border: pink solid 3px;
    transition: background 0.2s;
    width: 83%;
}

.productdisplay button:hover {
    background: ivory;
    color: black;
}

.productdisplay p span {
    font-weight: 600;
}

.addtowishlist {
    display: flex;
    align-items: center;
    gap: 7px;
    margin-top: -20px;
    margin-bottom: 20px;
}

.like {
    cursor: pointer;
    color: pink;
    font-size: 20px;
    transition: transform 0.2s;
}

.like:hover {
    transform: scale(1.5) ;
}

.popup {
    position: absolute;
    background: ivory;
    padding: 20px;
    top: 307px;
    left: 190px;
    border: 2px solid pink;
    animation: bounceIn 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55) forwards;
}

@keyframes bounceIn {
    0% {
        transform: translate(0, -150%);
        opacity: 0;

    }
    70% {
        transform: translate(0, -30%);

    }
    100% {
        transform: translate(0, -20%);
        opacity: 1;
    }
    
}


@media (max-width: 1024px) {

    .productdisplay-left img{
        width: 510px;
        height: 650px; 
    }

    .productdisplay {
        gap: 70px;
    } 

    .productdisplay-right h1 {
        font-size: 28px;
    }

    .sizes {
        display: flex;
        gap: 15px;
        font-size: 15px;
        overflow-x: auto;
       
    }

    .sizes::-webkit-scrollbar {
        display: none;
    }
    
    .productdisplay-right-description {
        width: 88%;
    }

    .productdisplay-right-description br {
        display: none;
    }
} 

@media (max-width: 1280px) {

    .popup {
        top: 260px;
    }
}

@media (max-width: 1024px) {

    .popup {
        top: 242px;
    }
}

@media (max-width: 800px) {

    .popup {
        top: 160px;
    }

    .productdisplay {
        flex-direction: column;
        gap: 30px;
    }

    .productdisplay-left img{
        width: 650px;
        height: 650px; 
    }

    .productdisplay button {
        width: 65%;
    }

}

@media (max-width: 500px) {

    .popup {
        font-size: 12px;
    }

    .productdisplay {
        flex-direction: column;
        gap: 10px;
    }
    
    .productdisplay-left img{
        width: 450px;
        height: 550px; 
    }
}

@media (max-width: 400px) {

    .popup {
        font-size: 12px;
    }

    .productdisplay {
        flex-direction: column;
        gap: 10px;
    }
    
    .productdisplay-left img{
        width: 350px;
        height: 450px; 
    }

    .productdisplay-right h1 {
        font-size: 25px;
    }

    .productdisplay-right-star,.productdisplay-right-prices{
        font-size: 17px;
    }

    .productdisplay-right-description {
        font-size: 15px;
    }

    .productdisplay button {
        padding: 25px;
        font-size: 17px;
        width: 75%;
    }

    .productdisplay-right-category {
        font-size: 15px;    
    }
    
}